
.left_stop_icon{
    position: absolute;
    top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	width:24px;
    z-index: 1;
    font-size: 30px;
    border-radius: 50% 50%;
    text-align: center;
    left: -1px;
	cursor: pointer;
	display: none;
}
.right_stop_icon{
    position: absolute;
    top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	width:24px;
    z-index: 1;
    font-size: 30px;
    right:0px;
    left: inherit;
	cursor: pointer;
	display: none;
}
.is-west-collapsible .left_stop_icon {
	display: block;
}
.is-east-collapsible .right_stop_icon{
	display: block;
}
.shrink_left_open{
    background: url('../icons/shrink_left_open.png');
    display: block;
    height: 104px;
    background-Size: cover;
    width: 24px;
}
.shrink_left_close {
    background: url('../icons/shrink_left_stop.png');
    display: block;
    height: 104px;
    background-Size: cover;
    width: 24px;
}
.shrink_right_open{
    background: url('../icons/shrink_right_open.png');
    display: block;
    height: 104px;
    background-Size: cover;
    width: 24px;
}
.shrink_right_close {
    background: url('../icons/shrink_right_stop.png');
    display: block;
    height: 104px;
    background-Size: cover;
    width: 24px;
}



